import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { EclassServiceService } from 'app/eclass-service.service';
import { Lesson } from 'app/schema/lesson';

@Component({
  selector: 'app-bookdetail',
  templateUrl: './bookdetail.component.html',
  styleUrls: ['./bookdetail.component.css']
})
export class BookdetailComponent implements OnInit {
  lessons : Lesson[];
  book : String;
  bookid : number;
  constructor(private route :ActivatedRoute, private eclassService : EclassServiceService) { }

  ngOnInit(): void {
    const id = Number(this.route.snapshot.paramMap.get('bookid'));
    this.book = this.route.snapshot.queryParams["book"];
    this.bookid = Number.parseInt(this.route.snapshot.queryParams["bookid"]);
    this.eclassService.getLessons(id).subscribe(lessons => this.lessons=lessons);
  }

}
