import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FamBookService } from 'app/fam-book.service';
import { Feed } from 'app/schema/feed';

@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.css']
})
export class TimelineComponent implements OnInit {

  constructor(private route: ActivatedRoute, private fambookservice : FamBookService) { }
  feeds : Feed[] ;
  id : number;
  ngOnInit(): void {
    this.id = this.route.snapshot.queryParams["id"];
    this.fambookservice.getFeeds(this.id).subscribe(feeds => this.feeds = feeds);
  }
  getFeeds(){

  }
}
