import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BroadcastMsgService } from 'app/broadcast-msg.service';
import { NavbarComponent } from 'app/components/navbar/navbar.component';
import { EclassServiceService } from 'app/eclass-service.service';
import { Book } from 'app/schema/book';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {
  books : Book[];
  class: String;
  constructor(private route: ActivatedRoute,private eclassService : EclassServiceService, private broadcastSV : BroadcastMsgService  ) { }
  
  ngOnInit(): void {
    const id = Number(this.route.snapshot.paramMap.get('eclassid'));
    this.class = this.route.snapshot.queryParams["class"];
    this.eclassService.getBooks(id).subscribe(books => this.books = books);
  }

}
