import { Routes } from "@angular/router";
import { BookComponent } from "app/book/book.component";
import { BookdetailComponent } from "app/bookdetail/bookdetail.component";
import { EclassComponent } from "app/eclass/eclass.component";
import { TimelineComponent } from "app/timeline/timeline.component";
import { VideoplayerComponent } from "app/videoplayer/videoplayer.component";

export const EclassRoutes: Routes = [
    { path: '',      component: EclassComponent },
    { path: 'book/:eclassid',      component: BookComponent },
    { path: 'book/detail/:bookid',      component: BookdetailComponent },
    { path: 'videoplayer/:videoid',      component: VideoplayerComponent },
    { path: 'feed/:id',      component: TimelineComponent },
];