import { Component, Input, OnChanges, OnInit, ViewChild, ɵPlayer } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { YouTubePlayer } from '@angular/youtube-player';
import { EclassServiceService } from 'app/eclass-service.service';
import { Lesson } from 'app/schema/lesson';
import { Observable } from 'rxjs';

let apiLoaded = false;
@Component({
  selector: 'app-videoplayer',
  templateUrl: './videoplayer.component.html',
  styleUrls: ['./videoplayer.component.css']
})
export class VideoplayerComponent implements OnInit {
  videoid: String;
  lesson : String;
  bookid : number;
  lessons : Lesson[];
  ytplayer : YT.Player; 
  constructor(private rout : ActivatedRoute, private eclassService : EclassServiceService) { }
  @ViewChild(YouTubePlayer) player : YouTubePlayer;
  ngOnInit(): void {
    this.videoid = this.rout.snapshot.paramMap.get("videoid");
    this.lesson = this.rout.snapshot.queryParams["lesson"];
    this.bookid = Number.parseInt(this.rout.snapshot.queryParams["bookid"]);
    if (!apiLoaded) {
      // This code loads the IFrame Player API code asynchronously, according to the instructions at
      // https://developers.google.com/youtube/iframe_api_reference#Getting_Started
      const tag = document.createElement('script');
      tag.src = 'https://www.youtube.com/iframe_api';
      document.body.appendChild(tag);
      apiLoaded = true;
    }
    this.eclassService.getLessons(this.bookid).subscribe(lessons => this.lessons=lessons);
  }

  resizeYTIframe(): void {
    let iframe = document.getElementsByTagName("iframe");
    iframe[0].setAttribute("width","100%");
  }
  ready(event : YT.PlayerEvent): void{
    this.ytplayer = event.target;
    event.target.playVideo();
  }
  onStageChanged(event : YT.OnStateChangeEvent): void {
    if(event.data == YT.PlayerState.UNSTARTED){
      this.resizeYTIframe();
    }
  }
  selectedLesson: Lesson;
  playVideo(lesson :Lesson): void{
    this.videoid = lesson.youtubeVideo;
    this.lesson = lesson.name;
    this.selectedLesson = lesson;
    this.ytplayer.playVideo();
  }
}
