import { Component, OnInit } from '@angular/core';
import { EclassServiceService } from 'app/eclass-service.service';
import {Eclass} from '../schema/eclass'
@Component({
  selector: 'app-eclass',
  templateUrl: './eclass.component.html',
  styleUrls: ['./eclass.component.css']
})
export class EclassComponent implements OnInit {
  eclasses : Eclass[];
  class: String = "from class";
  childValue: String = "init";
  constructor(private eclassService : EclassServiceService) { 
  }
  change(msg : String) : void {
    this.childValue = msg;
    debugger;
  }
  ngOnInit(): void {
    this.eclassService.getEclass().subscribe((datas=>this.eclasses = datas));
  }

}
