import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Eclass} from './schema/eclass';
import { HttpHeaders } from '@angular/common/http';
import { Book } from './schema/book';
import { Lesson } from './schema/lesson';
const httpOptions = {
  headers: new HttpHeaders({
    //'Content-Type':'application/json',
    'Accept': 'application/json, text/plain, */*',
  }),
  'withCredentials':false
};
@Injectable({
  providedIn: 'root'
})
export class EclassServiceService {

  constructor(private httpClient : HttpClient) { }
  param : HttpParams = new HttpParams();
  con : boolean=true;
  getEclass():Observable<Eclass[]> {
    /*
     this.httpClient.get<Eclass[]>("http://localhost:8080/eclass/0").subscribe((datas: Eclass[]) =>{
       for(var i=0;i<datas.length;i++)
        console.log(datas[i].name);
     });*/
     return this.httpClient.get<Eclass[]>("http://localhost:8080/eclass/0",httpOptions);
  }
  getBooks(eclassId:number):Observable<Book[]>{
    return this.httpClient.get<Book[]>("http://localhost:8080/book/" + eclassId,httpOptions);
  }
  getLessons(bookid:number): Observable<Lesson[]>{
    return this.httpClient.get<Lesson[]>("http://localhost:8080/book/detail/" + bookid,httpOptions);
  }
}
